import React from 'react'
import {Link, Navigate, useNavigate, NavigateFunction } from 'react-router-dom';

interface HomePageProps{

}

const HomePage: React.FC<HomePageProps> = () => {
  const storedUser = sessionStorage.getItem('user');
  const navigate: NavigateFunction = useNavigate();


  return storedUser ? (
      <div className='HomePage'>
        <h1>Welcome</h1>

        <p>      </p>
        
        <div className='Test'>
          <Link to="https://Shahzod999.github.io/Auth/test">Start <br /> test</Link>
        </div>
        
        <button className="btn third2" onClick={()=>{sessionStorage.removeItem('user'); navigate('https://Shahzod999.github.io/Auth/');}}>
          Log out
        </button>
      </div>
    ) : (<Navigate to="https://Shahzod999.github.io/Auth/login" />);
};

export default HomePage